<template>
  <div>
    <div class="item">
      <div class="up">
        <h4 class="txt1">
          {{ value.titulo }}
        </h4>
        <v-icon
          v-animate-onscroll.repeat="{ down: 'animated bounceIn' }"
          x-large
          color="verde"
        >
          {{ value.icono }}
        </v-icon>
        <p>
          {{ value.texto }}
        </p>
      </div>
      <div class="bottom">
        <v-btn
          class="success"
          :to="value.to"
          :href="value.href"
        >
          <span>{{ value.textoBoton }}</span>
        </v-btn>
      </div>
    </div>
    <div v-if="value.index !== value.total - 1">
      <hr>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      value: {
        titulo: {
          type: Text,
          default: '',
        },
        icono: {
          type: Text,
          default: '',
        },
        texto: {
          type: Text,
          default: '',
        },
        textoBoton: {
          type: Text,
          default: '',
        },
        index: {
          type: Number,
          default: '',
        },
        total: {
          type: Number,
          default: '',
        },
        to: {
          type: Text,
          default: '',
        },
        href: {
          type: Text,
          default: '',
        },
        type: Object,
        default: null,
      },
    },
  }
</script>
<style scoped>
.item {
  width: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.item .txt2 {
  margin-bottom: 20px;
  color: green;
}

.item > .txt1,
.main > .item > p {
  color: #595959;
}

.item .up > p,
.main .item > p {
  margin-top: 20px;
}

.item .v-icon {
  margin-top: 20px;
}

.item .v-btn {
  border-radius: 20px;
  text-transform: none !important;
}
hr {
  width: 80%;
  margin: 0px auto;
}
</style>
